import logo from './logo.svg';
// import './App.css';
import React, { useEffect } from 'react';
import {
  Scene,
  WebGLRenderer,
  PerspectiveCamera,
  BoxGeometry,
  PlaneGeometry,
  DoubleSide,
  MeshBasicMaterial,
  Mesh,
  DirectionalLight,
  AmbientLight,
  Color,
  FogExp2,
  CylinderGeometry,
  PCFShadowMap,
  MeshPhongMaterial,
  SkeletonHelper,
  AnimationMixer,
  Clock,
} from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';

let mixer;
let model;
let clock;

const add = (scene) => {
  const geometry = new BoxGeometry(1, 1, 1);
  const material = new MeshPhongMaterial({ color: 0xffffff, flatShading: true });
  for (let index = 0; index < 500; index++) {
    const cube = new Mesh(geometry, material);
    cube.position.set(Math.random() * 200 - 100, 0.5, Math.random() * 200 - 100);
    // cube.updateMatrix();
    // cube.matrixAutoUpdate = false;
    cube.castShadow = true;
    scene.add(cube);
  }
}

function setWeight(action, weight) {

  action.enabled = true;
  action.setEffectiveTimeScale(1);
  action.setEffectiveWeight(weight);

}

const addroobot = (scene,render) => {
  const loader = new GLTFLoader();
  loader.load(process.env.PUBLIC_URL + '/Soldier.glb', (gltf) => {
    model = gltf.scene;
    scene.add(model);
    model.traverse((object) => object.isMesh && (object.castShadow = true));
    //
    const skeleton = new SkeletonHelper(model);
    skeleton.visible = false;
    scene.add(skeleton);
    //
    const animations = gltf.animations;
    mixer = new AnimationMixer(model);
    const idleAction = mixer.clipAction(animations[0])
    const walkAction = mixer.clipAction(animations[3])
    const runAction = mixer.clipAction(animations[1])
    runAction.play();
    render();
  })
};

const light = (scene) => {
  const dirLight1 = new DirectionalLight(0xffffff);
  dirLight1.castShadow = true;
  dirLight1.position.set(0, 200, -130);
  scene.add(dirLight1);
  const ambientLight = new AmbientLight(0x222222, 1);
  scene.add(ambientLight);
}

const init = (scene, renderer, camera, controls) => {
  requestAnimationFrame(() => init(scene, renderer, camera, controls));
  const time = 0.75 * clock.getDelta();
  mixer.update(time);
  controls.update();
  renderer.render(scene, camera);
}

const floor = (scene) => {
  const fl = new PlaneGeometry(200, 200);
  const material = new MeshPhongMaterial({ color: '#ccc', side: DoubleSide });
  const plane = new Mesh(fl, material);
  plane.receiveShadow = true;
  plane.castShadow = false;
  plane.rotation.x = Math.PI / 2;
  // scene.add(plane);
}



function App() {
  useEffect(() => {
    const scene = new Scene();
    scene.background = new Color(0xcccccc);
    scene.fog = new FogExp2(0xcccccc, 0.002);
    const renderer = new WebGLRenderer({ antialias: true });
    renderer.shadowMap.enabled = true;
    renderer.shadowMap.type = PCFShadowMap;
    renderer.setPixelRatio(window.devicePixelRatio);
    renderer.setSize(window.innerWidth, window.innerHeight);
    const camera = new PerspectiveCamera(20, window.innerWidth / window.innerHeight, 1, 1000);
    camera.position.set(10, 5, 0);
    // camera.lookAt(0, 0, 0);
    const controls = new OrbitControls(camera, renderer.domElement);
    controls.enableDamping = true; // an animation loop is required when either damping or auto-rotation are enabled
    controls.dampingFactor = 0.05;
    controls.screenSpacePanning = false;
    controls.minDistance = 10;
    controls.maxDistance = 50;
    controls.maxPolarAngle = Math.PI / 2;
    controls.listenToKeyEvents(window);
    /** 将渲染器的输出添加到html元素 */
    const app = document.getElementById("App");
    app.children.length === 0 && app.appendChild(renderer.domElement);
    clock = new Clock();
    floor(scene);
    // add(scene);
    light(scene);
    addroobot(scene, () => init(scene, renderer, camera, controls));
  }, [])
  return (
    <div id="App" />
  );
}

export default App;
